.App {
  min-height:100vh;
  background-image: linear-gradient(60deg, #abecd6 0%, #fbed96 100%);
}

.search-form {
  min-height: 10vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.search-bar {
  width: 50%;
  border: none;
  padding: 10px;
} 

.Search-button {
  background: rgba(158, 106, 28, 0.897);
  border: none;
  padding: 10px 20px;
  color: white;
}

.recipes {
 display: flex;
 justify-content: space-around;
 flex-wrap: wrap; 
}